<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process232"></a>

<p><br></p>
<p><span class="rvts17">勇士的错觉（</span><span class="rvts50">The Delusion of Grandeur</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:305,Y:395</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：瑞嘉瑞玛（</span><span class="rvts20">Rajjarima</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">说服恶魔或打败恶魔</span>
<br><span class="rvts83"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1313.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">血月岛中部有</span><span class="rvts20">3</span><span class="rvts21">处雕像，破坏雕像的前提是先要找到一本叫做《华丽诗集》的书，这本书位于血月岛的很多地方。其中，小岛东北部有一处黑环势力占领的区域，有这本书。在血月岛大树的黑环团伙中，也有这本书。</span>
<br><span class="rvts21">另外，在接贾韩的支线</span><span class="rvts20"></span><span class="rvts21">邪恶之物的猎人（</span><span class="rvts20">A Hunter of Wicked Thing</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的屋子内，也有这本书。这本书需要有</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">标签的角色读懂后，才可以用于破除雕像的恶魔。</span>
<br><span class="rvts21">接着使用必须是携带银质杠杆的角色与门阀互动，才可以成功合成杠杆门阀。使用杠杆前，需要使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">查看守卫洞穴的灵魂，说服他或杀掉他，才会使他失去对杠杆的源力控制，这样才能打开对应的石门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1314.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">这个恶魔原来是个神醒者，也是被恶魔占据了身体。开局可以尝试说服它免除战斗（可以先不解除他身上的枷锁），这需要</span><span class="rvts20">4</span><span class="rvts21">点的说服值（</span><span class="rvts20">Persuasion</span><span class="rvts21">）和</span><span class="rvts20">13</span><span class="rvts21">点的体质（</span><span class="rvts20">Constitution</span><span class="rvts21">）。如果和他发生战斗，那么他会在第一轮瞬间秒掉离他最近的角色。</span>
<br><span class="rvts21">另外，想要得到门外看守者灵魂的奖励，需要在不杀掉看守者的前提下，选择</span><span class="rvts20"></span><span class="rvts21">我不会与恶魔谈话的</span><span class="rvts20"></span><span class="rvts21">选项。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



